body {
  margin: 0;
  background-color: black;
}

#arrowkeys {
  position: absolute;
  bottom: 2vh;
  left: 40vw;
  & img {

    animation: pulse 3s linear infinite;
    width: 20vw;

    -webkit-filter: drop-shadow(1px 1px 0 #ffffff)
    drop-shadow(-1px -1px 0 #ffffff);
    filter: drop-shadow(1px 1px 0 #ffffff)
    drop-shadow(-1px -1px 0 #ffffff);
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

#yes {
  position: absolute;
  top: 30vh;
  left: 20vw;
  filter: drop-shadow(0 0 0.25rem whitesmoke);
}

#no {
  position: absolute;
  top: 30vh;
  right: 20vw;
  filter: drop-shadow(0 0 0.25rem whitesmoke);
}

#yes:hover, #no:hover{
  -webkit-animation:spin 4s linear infinite;
  -moz-animation:spin 4s linear infinite;
  animation:spin 4s linear infinite;
  filter: drop-shadow(0 0 0.75rem whitesmoke);
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

#clickSpace {
  position: absolute;
  top: unset;
  bottom: 5vh;
  font-size: 2vw;
  left: 10vw;
}

.text {
  position: absolute;
  margin-right: auto;
  margin-left: auto;
  top: 20vh;
  transform: translate(50%,-50%);
  text-transform: uppercase;
  font-family: verdana;
  font-size: 4vw;
  font-weight: 700;
  color: #f5f5f5;
  text-shadow: 1px 1px 1px #919191,
  1px 2px 1px #919191,
  1px 3px 1px #919191,
  1px 4px 1px #919191,
  1px 5px 1px #919191,
  1px 6px 1px #919191,
  1px 7px 1px #919191,
  1px 8px 1px #919191,
  1px 9px 1px #919191,
  1px 10px 1px #919191,
  1px 18px 6px rgba(16,16,16,0.4),
  1px 22px 10px rgba(16,16,16,0.2),
  1px 25px 35px rgba(16,16,16,0.2),
  1px 30px 60px rgba(16,16,16,0.4);
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.textbox#inpD {
  top: 50vh;
  right: 15vw;
  width: 35vw;
  height: 15vh;
  position: absolute;
  transform-style: preserve-3d;
  -webkit-animation: turnaround 20s infinite;
  animation: turnaround 20s infinite;
}

.textbox#inpR {
  top: 50vh;
  left: 20vw;
  width: 35vw;
  height: 15vh;
  position: absolute;
  transform-style: preserve-3d;
  -webkit-animation: turnaround 20s infinite;
  animation: turnaround 20s infinite;
}
.textbox-box {
  background-color: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: inset 0 0 0 3px #6e3495;
  position: relative;
  -webkit-animation: hover 2s alternate infinite;
  animation: hover 2s alternate infinite;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.2s ease;
}
.textbox-field {
  display: flex;
  flex-direction: column;
  padding: 10px 15px 15px;
  align-self: stretch;
  border-radius: 5px;
}
.textbox-action {
  height: 80px;
  width: 80px;
  min-width: 80px;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  -border-radius: 100px;
  transform-style: preserve-3d;
  -webkit-animation: hover 2s -0.4s alternate infinite;
  animation: hover 2s -0.4s alternate infinite;
  background-color: #e3e3e3;
  position: relative;
  transition: all 0.2s ease;
}
.textbox-action svg {
  fill: #272343;
  width: 65%;
  height: 65%;
  -webkit-animation: hover 2s -0.6s alternate infinite;
  animation: hover 2s -0.6s alternate infinite;
  -webkit-filter: drop-shadow(0 0 15px rgba(0, 0, 0, 0.5));
}
.textbox-action .textbox-face {
  background-color: #000000;
  transition: all 0.2s ease;
}
.textbox-action .textbox-side {
  width: 10px;
}
.textbox-action .textbox-bottom,
.textbox-action .textbox-top {
  height: 10px;
}
.textbox-action:hover {
  cursor: pointer;
  background-color: #43bbc6;
}
.textbox-action:hover .textbox-face {
  background-color: #000000;
}
.textbox-text {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  font: 3vw sans-serif;
  -webkit-animation: hover 2s -0.4s alternate infinite;
  animation: hover 2s -0.4s alternate infinite;
  display: block;
  color: #272343;
  -webkit-filter: drop-shadow(0 0 15px rgba(0, 0, 0, 0.5));
}
.textbox-text::-moz-placeholder {
  color: #272343;
  opacity: 0.5;
}
.textbox-text:-ms-input-placeholder {
  color: #272343;
  opacity: 0.5;
}
.textbox-text::placeholder {
  color: #272343;
  opacity: 0.5;
}
.textbox-text:focus {
  outline: none;
}
.textbox-face {
  position: absolute;
  background-color: #4c2566;
  left: 0;
}
.textbox-side {
  transform: rotateY(90deg);
  height: 100%;
  width: 20px;
  top: 0;
  transform-origin: 0% 50%;
}
.textbox-bottom {
  transform: rotateX(90deg);
  height: 20px;
  width: 100%;
  bottom: 0;
  transform-origin: 50% 100%;
}
.textbox-top {
  transform: rotateX(-90deg);
  height: 20px;
  width: 100%;
  top: 0;
  transform-origin: 50% 0;
}

@-webkit-keyframes hover {
  from {
    transform: translateZ(10px);
  }
  to {
    transform: translateZ(20px);
  }
}

@keyframes hover {
  from {
    transform: translateZ(10px);
  }
  to {
    transform: translateZ(20px);
  }
}
@-webkit-keyframes turnaround {
  0% {
    transform: perspective(500px) rotateY(70deg) rotateZ(-10deg) rotateX(30deg);
  }
  33% {
    transform: perspective(500px) rotateY(20deg) rotateZ(-10deg) rotateX(-30deg);
  }
  67% {
    transform: perspective(0) rotateY(0deg) rotateZ(0deg) rotateX(0deg);
  }
  100% {
    transform: perspective(500px) rotateY(70deg) rotateZ(-10deg) rotateX(30deg);
  }
}
@keyframes turnaround {
  0% {
    transform: perspective(500px) rotateY(70deg) rotateZ(-10deg) rotateX(30deg);
  }
  33% {
    transform: perspective(500px) rotateY(20deg) rotateZ(-10deg) rotateX(-30deg);
  }
  67% {
    transform: perspective(0) rotateY(0deg) rotateZ(0deg) rotateX(0deg);
  }
  100% {
    transform: perspective(500px) rotateY(70deg) rotateZ(-10deg) rotateX(30deg);
  }
}

.box {
  position: absolute;
  bottom: 5vh;
  left: 35vw;
  font-size: 1.5vw;
  font-family: "Agency FB", Arial, sans-serif;
  border-radius: 2px;
  padding: 0.8vw;
  width: 30vw;
  height: 14vh;
  margin: auto;
  background: white;
  border: 1px solid white;
  box-shadow: 0 1px 0 1px black,
  inset 0 1px 0 1px black,
  0 0 0 1px black,
  inset 0 0 0 1px black;
}
.box > i {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: black;
  margin-top: 23px;
  animation: bounce 0.5s ease-in-out 0 infinite alternate;
  float: right;
}

@keyframes bounce {
  from { margin-top: 20px; }
  to { margin-top: 23px; }
}
